import { ActionIcon, Button, LoadingOverlay, Popover, SegmentedControl, TagsInput, Text, Tooltip } from "@mantine/core";
import { DatePicker, DatesRangeValue } from "@mantine/dates";
import { computed, signal } from "@preact/signals";
import dayjs from "dayjs";
import { LayoutGrid, Sheet, X } from "lucide-react";
import { ReactElement } from "preact/compat";
import toast from "react-hot-toast";
import { FILTER_CATEGORIES, FILTER_CATEGORIES_OBJ, VIEWS, ViewType } from "../../Common/CommonModel";
import { defaultTripleMissions } from "../../context/data";
import { globalState, xsSm } from "../../context/GlobalState";
import type { CoordinatorTableView as _CoordinatorTableView } from "../../pages/exports_";
import { Horizontal, Vertical } from "../../utils/ComponentToolbox";
import type { CoordinatorCardView as _CoordinatorCardView } from "./CoordinatorCardView";

const VIEWS_ICONS_OBJ: Record<ViewType, ReactElement> = {
	card: (
		<Tooltip label="Card view" position="top">
			<LayoutGrid size={16} />
		</Tooltip>
	),
	table: (
		<Tooltip label="Table view" position="top">
			<Sheet size={16} />
		</Tooltip>
	),
};

const isViewLoading = signal(false);

/**
 * @notExported
 * Renders a {@link VIEWS | `view`} selection allowing the user to switch between the {@link _CoordinatorCardView | `card view`}
 * and the {@link _CoordinatorTableView | `table view`}.
 * @returns The rendered view selection component.
 */
const CoordinatorViewRender = () => (
	<div style={{ position: "relative" }}>
		<SegmentedControl
			data={VIEWS.map((view) => ({ value: view, label: VIEWS_ICONS_OBJ[view] }))}
			size={xsSm.value}
			style={{ border: `solid 1px ${globalState.colorScheme.value === "dark" ? "#424242" : "#d0d4d9"}` }}
			value={globalState.view.value}
			onChange={(value) => {
				isViewLoading.value = true;
				setTimeout(() => {
					globalState.view.value = value as ViewType;
					isViewLoading.value = false;
					toast.success(`Switched to ${value} view`, { duration: 2000 });
				}, 100);
			}}
		/>
		<LoadingOverlay visible={isViewLoading.value} />
	</div>
);

const PREFIXED_FILTER_CATEGORIES = FILTER_CATEGORIES.map((category) => `@${category}`);

/**
 * @notExported
 * The state of the filter zone.
 */
const filterState = {
	/** The current filter search value. */
	filterSearchValue: signal(""),
	/** The current filter value. */
	filterValue: signal<string[]>([]),
	/** The current filter category. */
	category: signal(""),
	/** The current filter options. */
	options: signal<string[]>([]),
	filterDateRange: signal<DatesRangeValue>([null, null]),
	hoveredFilterDateRange: signal<DatesRangeValue>([null, null]),
};

const updateHoveredFilterDateRange = (_: MouseEvent, date: Date) => {
	if (!filterState.filterDateRange.value[0]) filterState.hoveredFilterDateRange.value = [null, null];
	else if (filterState.filterDateRange.value[1])
		filterState.hoveredFilterDateRange.value = [filterState.filterDateRange.value[0], filterState.filterDateRange.value[1]];
	else if (filterState.filterDateRange.value[0] > date)
		filterState.hoveredFilterDateRange.value = [date, filterState.filterDateRange.value[0]];
	else filterState.hoveredFilterDateRange.value = [filterState.filterDateRange.value[0], date];
};

const filterDateRangeString = computed(
	() =>
		`${filterState.filterDateRange.value[0] ? dayjs(filterState.filterDateRange.value[0]).format("DD/MM/YYYY") : "___"} - ${
			filterState.filterDateRange.value[1] ? dayjs(filterState.filterDateRange.value[1]).format("DD/MM/YYYY") : "___"
		}`
);

const routeOptions = (async () =>
	Array.from(
		new Set(
			defaultTripleMissions
				.flatMap((mission) => [mission.departure, ...mission.steps_, mission.arrival])
				.map((place) => place.libelle)
		)
	))();

/**
 * @notExported
 * Renders a filter input allowing the user to edit the filter values. \
 * You can filter by {@link FILTER_CATEGORIES | category} by prefixing the filter value with `@`.
 * @returns The rendered filter input component.
 */
const CoordinatorFilterInput = () => (
	// Portal kept for Date in order to work with the Table view
	<Popover withArrow position="bottom-start" opened={filterState.category.value === FILTER_CATEGORIES_OBJ.date}>
		<Popover.Target>
			{/* Portal kept for Filter input in order to work with the Table view */}
			<TagsInput
				placeholder={'Filter (@ for category, " for space escape)'}
				style={{ flexGrow: 1 }}
				size={xsSm.value}
				splitChars={filterState.filterSearchValue.value.startsWith('"') ? ['"'] : [" "]}
				data={filterState.filterSearchValue.value.startsWith("@") ? PREFIXED_FILTER_CATEGORIES : filterState.options.value}
				searchValue={
					filterState.category.value === FILTER_CATEGORIES_OBJ.date
						? filterDateRangeString.value
						: filterState.filterSearchValue.value
				}
				onSearchChange={(searchValue) =>
					filterState.category.value !== FILTER_CATEGORIES_OBJ.date && (filterState.filterSearchValue.value = searchValue)
				}
				value={filterState.filterValue.value}
				onChange={(value) => {
					if (filterState.category.value === FILTER_CATEGORIES_OBJ.date) return;
					filterState.options.value = [];
					if (PREFIXED_FILTER_CATEGORIES.includes(value.at(-1) ?? "")) {
						filterState.category.value = value.at(-1)?.slice(1) ?? "";
						switch (filterState.category.value) {
							case FILTER_CATEGORIES_OBJ.route:
								routeOptions.then((options) => (filterState.options.value = options));
								break;
							case FILTER_CATEGORIES_OBJ.date:
								break;
						}
					} else if (!value.at(-1)?.startsWith("@")) {
						filterState.filterValue.value = value.map((v, i) =>
							filterState.category.value && i === value.length - 1 ? `${filterState.category.value} | ${v}` : v
						);
						filterState.category.value = "";
					}
				}}
				leftSection={
					filterState.category.value && (
						<Horizontal justifyContent="space-between" width={"100%"} margin="0 10px">
							<Text size={xsSm.value} style={{ flexGrow: 1 }}>
								{filterState.category.value}
							</Text>
							<ActionIcon variant="default" size={xsSm.value}>
								<X onClick={() => (filterState.category.value = "")} />
							</ActionIcon>
						</Horizontal>
					)
				}
				leftSectionWidth={filterState.category.value ? 125 : 0}
				clearable
			/>
		</Popover.Target>
		<Popover.Dropdown>
			<DatePicker
				type="range"
				onChange={(value) => (filterState.filterDateRange.value = value)}
				__onDayMouseEnter={updateHoveredFilterDateRange}
				getDayProps={(date) => {
					if (!dayjs().isSame(date, "date")) return {};
					if (
						filterState.hoveredFilterDateRange.value[0] &&
						filterState.hoveredFilterDateRange.value[1] &&
						dayjs(date).add(1, "day").isAfter(filterState.hoveredFilterDateRange.value[0], "date") &&
						dayjs(date).add(-1, "day").isBefore(filterState.hoveredFilterDateRange.value[1], "date")
					)
						return {
							style: {
								backgroundColor: "var(--mantine-color-red-light)",
								color: "var(--mantine-color-white)",
							},
						};

					return {
						style: {
							backgroundColor: "var(--mantine-color-red-filled)",
							color: "var(--mantine-color-white)",
						},
					};
				}}
			/>
			<Horizontal justifyContent="flex-end">
				<Button
					size={xsSm.value}
					onClick={() => {
						filterState.filterValue.value.push(`${filterState.category.value} | ${filterDateRangeString.value}`);
						filterState.filterDateRange.value = [null, null];
						filterState.filterSearchValue.value = "";
						filterState.category.value = "";
					}}
				>
					Validate
				</Button>
			</Horizontal>
		</Popover.Dropdown>
	</Popover>
);

/**
 * Renders the filter zone, with a {@link CoordinatorViewRender | `view selection`} and a {@link CoordinatorFilterInput | `filter input`}.
 * @returns The rendered filter zone component.
 */
export const CoordinatorFilterZone = () => (
	<Vertical gap={5} marginTop={10}>
		<Horizontal gap={8}>
			<CoordinatorViewRender />
			<CoordinatorFilterInput />
		</Horizontal>
	</Vertical>
);
