import { useVirtualizer, VirtualItem } from "@tanstack/react-virtual";
import { useRef } from "preact/compat";

/**
 * Custom hook to add extra items to keep displayed to the virtualizer.
 * @example
 * // the style should look like this for table rows
 * <div style={{
 * 	height: virtualRow.size,
 * 	transform: `translateY(${virtualRow.start - (virtualRow.index - startExpandedIndex) * virtualRow.size}px)`,
 * }} />
 * // or like this for other elements
 * <div style={{
 * 	height: virtualRow.size, // reduce the size for adding gap
 * 	transform: `translateY(${virtualRow.start}px)`,
 * }} />
 * @param props The virtualizer options and the number of extra items to keep displayed.
 * @returns The virtualizer and the extra items to keep displayed and the start index of the extra items.
 */
export const useExtraVirtualizer = <TScrollElement extends Element, TItemElement extends Element>(
	props: Parameters<typeof useVirtualizer<TScrollElement, TItemElement>>[0] & { extraItemCount: number }
) => {
	const { extraItemCount, ...virtualizerOptions } = props;

	const rowVirtualizer = useVirtualizer(virtualizerOptions);

	// virtualize items with some extra items on each side
	const expandedVirtualizeItems = useRef<VirtualItem[]>([]);

	// update the virtualized items
	for (const virtualItem of rowVirtualizer.getVirtualItems()) expandedVirtualizeItems.current[virtualItem.index] = virtualItem;

	// remove the items that are some items away from the current range
	const endExpandedIndex = (rowVirtualizer.range?.endIndex ?? 0) + extraItemCount;
	let i = endExpandedIndex + 1;
	while (expandedVirtualizeItems.current[i]) {
		delete expandedVirtualizeItems.current[i];
		i++;
	}
	const startExpandedIndex = Math.max((rowVirtualizer.range?.startIndex ?? 0) - extraItemCount, 0);
	i = startExpandedIndex - 1;
	while (expandedVirtualizeItems.current[i]) {
		delete expandedVirtualizeItems.current[i];
		i--;
	}

	return { rowVirtualizer, expandedVirtualizeItems, startExpandedIndex };
};
